import React, { useState } from 'react';
import CustomModal from '../CustomModal/CustomModal';
import { ModalInput, ModalButton, ModalError } from './styled';

const AuthModal = ({ visible, onClose }) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [step, setStep] = useState('email'); 
  const [error, setError] = useState('');
  const [showPassword, setShowPassword] = useState(false);

  const validateEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const validatePassword = (password) => {
    const passwordRegex = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[!@#$%^&*(),.?":{}|<>]).{8,32}$/;
    return passwordRegex.test(password);
  };

  const handleEmailSubmit = async () => {
    if (!validateEmail(email)) {
      setError('  email');
      return;
    }
    
    //     email
    try {
      const response = await fetch(`/api/check-user?email=${email}`);
      if (response.status === 200) {
        const userExists = await response.json();
        if (userExists) {
          setStep('password');
        } else {
          setStep('registration');
        }
        setError('');
      } else {
        setError('-   ,  ');
      }
    } catch (error) {
      setError('-   ,  ');
    }
  };

  const handlePasswordSubmit = () => {
    if (!password) {
      setError(' ');
      return;
    }
    
    //   
    if (password === 'correctPassword') { 
      alert(' ');
      onClose();
    } else {
      setError(' ');
    }
  };

  const handleRegistration = async () => {
    if (!validatePassword(password)) {
      setError('    8  32 ,  ,   ');
      return;
    }

    if (password !== confirmPassword) {
      setError('  ');
      return;
    }
    
    //  
    try {
      const response = await fetch('/api/register', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ email, password })
      });

      if (response.status >= 200 && response.status < 300) {
        alert(' ');
        onClose();
      } else {
        setError('-   ,  ');
      }
    } catch (error) {
      setError('-   ,  ');
    }
  };

  const toggleShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const renderContent = () => {
    switch (step) {
      case 'email':
        return (
          <>
            <h2>  email</h2>
            <ModalInput
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Email"
            />
            <ModalButton onClick={handleEmailSubmit}></ModalButton>
            {error && <ModalError>{error}</ModalError>}
          </>
        );
      case 'password':
        return (
          <>
            <h2>  </h2>
            <ModalInput
              type={showPassword ? 'text' : 'password'}
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder=""
            />
            <ModalButton onClick={toggleShowPassword}>
              {showPassword ? ' ' : ' '}
            </ModalButton>
            <ModalButton onClick={handlePasswordSubmit}></ModalButton>
            {error && <ModalError>{error}</ModalError>}
          </>
        );
      case 'registration':
        return (
          <>
            <h2>  </h2>
            <ModalInput
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="Email"
              readOnly
            />
            <ModalInput
              type={showPassword ? 'text' : 'password'}
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder=""
            />
            <ModalInput
              type={showPassword ? 'text' : 'password'}
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              placeholder=" "
            />
            <ModalButton onClick={toggleShowPassword}>
              {showPassword ? ' ' : ' '}
            </ModalButton>
            <ModalButton onClick={handleRegistration}></ModalButton>
            {error && <ModalError>{error}</ModalError>}
          </>
        );
      default:
        return null;
    }
  };

  return (
    <CustomModal visible={visible} onClose={onClose}>
      {renderContent()}
    </CustomModal>
  );
};

export default AuthModal;
